.App {
  text-align: center;
}

div {
  box-sizing: border-box;
}

.list {
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid grey;
}

.modal-window {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding: 20px;
}

.header-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 16px;
  background: #FFF;
  line-height: 1;
}
.header-wrap .header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-wrap .header-container .home-link {
  display: flex;
  align-items: center;
  gap: 4px;
}
.header-wrap .header-container .home-link .link-text {
  font-weight: bold;
}
.header-height {
  height: 104px;
}

.nav-wrap {
  overflow-x: scroll;
  position: fixed;
  top: 56px;
  left: 0;
  width: 100%;
  padding: 8px 16px;
  border-top: 1px solid #0000001a;
  background: #FFF;
  box-sizing: border-box;
  line-height: 1;
}
.nav-wrap .nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}
.nav-wrap .nav-container .menu-button {
  padding: 4px 8px;
  border-radius: 4px;
  background: #0000000d;
}
.nav-wrap .nav-container .tab-button-list {
  display: flex;
  gap: 16px;
}
.nav-wrap .nav-container .tab-button-list .tab-button-item .tab-button {
  padding: 8px 16px;
  border-radius: 4px;
  background: #0000000d;
  white-space: nowrap;
}
